html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  color: #F2F2F2;
  font-family: "Cinzel", serif;
}

img {
  display: block;
  width: 100%;
}

p {
  line-height: 1.5;
}

.container {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  background-color: #1C1C1C;
}

.content-header {
  display: flex;
  padding: 1rem 0;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .content-header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.header-logo {
  display: flex;
  align-items: center;
}
.header-logo img {
  max-width: 32rem;
}
@media (min-width: 768px) {
  .header-logo img {
    height: 7rem;
    width: auto;
  }
}
.header-logo .icon-menu {
  filter: invert(100%);
  width: 5rem;
  margin-left: 1rem;
  padding: 0.5rem;
}
.header-logo .icon-menu:hover {
  cursor: pointer;
  background-color: #666666;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .header-logo .icon-menu {
    display: none;
  }
}

.main-nav {
  height: 0;
  opacity: 0;
  transition: all 0.3s ease;
}
.main-nav.show {
  height: 21.5rem;
  opacity: 1;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .main-nav {
    margin-top: 0;
    opacity: 1;
    height: auto;
  }
}
.main-nav a {
  text-decoration: none;
  font-size: 2rem;
  color: #F2F2F2;
  margin-bottom: 2rem;
  display: block;
  text-align: center;
  transition: color 0.3s ease;
  position: relative;
}
.main-nav a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -0.8rem;
  width: 100%;
  height: 0.4rem;
  background-color: #B22222;
  transform: scale(0);
  transition: transform 0.4s ease;
}
.main-nav a:hover::after {
  transform: scale(1);
}
@media (min-width: 768px) {
  .main-nav a {
    display: inline;
    margin: 0 1rem;
  }
}
.main-nav a:hover {
  color: #B22222;
}

.hero {
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #F2F2F2;
  position: relative;
  overflow: hidden; /* Asegura que el contenido adicional no se desborde */
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .hero {
    height: 92vh;
  }
}

.hero picture,
.hero img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0; /* Envía la imagen al fondo */
}

.hero::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Oscurece la imagen de fondo para mayor contraste */
  z-index: 1; /* Superposición oscura encima de la imagen */
}

.hero-content {
  position: relative;
  z-index: 2; /* Asegura que el contenido esté por encima de la imagen y la superposición oscura */
  padding: 0 1rem;
}
@media (min-width: 768px) {
  .hero-content {
    padding: 0 2rem;
  }
}

.hero h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.5rem;
  margin-bottom: 2.5rem;
}

.cta-button {
  background-color: #B22222;
  color: #F2F2F2;
  padding: 1.5rem 2.5rem;
  margin-top: 2rem;
  text-decoration: none;
  font-size: 1.5rem;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #871a1a;
}

.gallery {
  background-color: #F2F2F2;
  padding: 4rem 0;
}
.gallery h2 {
  margin-top: 0;
  color: #000000;
  text-align: center;
  font-size: 4rem;
}

.gallery-images {
  padding: 0;
}
.gallery-images li {
  list-style: none;
}
.gallery-images img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-bottom: 3rem;
  border-radius: 2rem;
  transition: all 0.3s ease;
}
.gallery-images img:hover {
  transform: scale(0.9) rotate(1deg);
}
@media (min-width: 768px) {
  .gallery-images img {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
  .gallery-images li:nth-child(1) {
    grid-column: 1/2;
  }
  .gallery-images li:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery-images li:nth-child(4) {
    grid-column: 1/2;
  }
  .gallery-images li:nth-child(5) {
    grid-column: 2/3;
  }
  .gallery-images li:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery-images li:nth-child(7) {
    grid-column: 1/3;
  }
  .gallery-images li:nth-child(8) {
    grid-column: 3/5;
  }
}

.about {
  padding: 4rem 0;
  background-color: #000000;
}
.about h2 {
  margin-top: 0;
  text-align: center;
  font-size: 4rem;
}
.about img {
  border-radius: 5rem;
  margin: 0 auto;
}
.about .about-content {
  text-align: center;
}
@media (min-width: 768px) {
  .about .about-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    text-align: left;
  }
}
.about .about-name {
  color: #B22222;
  text-align: center;
}
.about .about-text {
  display: flex;
  align-items: center;
  font-size: 2rem;
}

.services-section {
  padding: 4rem 0;
  background-color: #F2F2F2;
  color: #F2F2F2;
}
.services-section h2 {
  color: #000000;
  text-align: center;
  font-size: 4rem;
  margin-top: 0;
  margin-bottom: 4rem;
}
.services-section .services-items {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .services-section .services-items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .services-section .services-items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.services-section .service-item {
  background-color: #1C1C1C;
  padding: 2rem;
  border-radius: 1rem;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.services-section .service-item h3 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #F2F2F2;
}
.services-section .service-item p {
  font-size: 1.2rem;
}
.services-section .service-item:hover {
  transform: translateY(-0.5rem);
}

.blog-section {
  padding: 4rem 0;
  background-color: #000000;
  color: #F2F2F2;
}
.blog-section .section-title {
  text-align: center;
  font-size: 4rem;
  margin-bottom: 2rem;
  color: #F2F2F2;
}
.blog-section .blog-content {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .blog-section .blog-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .blog-section .blog-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.blog-section .blog-post {
  background-color: #1C1C1C;
  border-radius: 0.5rem;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.blog-section .blog-post img {
  width: 100%;
  height: 15rem;
  object-fit: cover;
  display: block;
}
.blog-section .blog-post .post-content {
  padding: 2rem;
}
.blog-section .blog-post .post-content .post-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #F2F2F2;
}
.blog-section .blog-post .post-content .post-excerpt {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}
.blog-section .blog-post .post-content .read-more {
  text-decoration: none;
  color: #B22222;
  font-weight: bold;
  transition: color 0.3s ease;
}
.blog-section .blog-post .post-content .read-more:hover {
  color: #F2F2F2;
}
.blog-section .blog-post:hover {
  transform: translateY(-0.5rem);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.footer {
  background-color: #1C1C1C;
  padding: 2rem;
}
.footer p {
  margin: 0;
  text-align: center;
}